@import url(https://fonts.googleapis.com/css?family=Dosis:800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  font-family: 'Dosis', sans-serif;
  margin: 0; }

.canvas {
  width: 100%;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center; }

.counter-panel {
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px;
  padding-left: 50px;
  padding-right: 50px;
  height: 250px;
  width: auto;
  position: absolute;
  bottom: 0px;
  /*    background:linear-gradient(rgba(156,39,176,.2),#fff); */
  background: linear-gradient(45deg, #4CCEB1, #E0F0CF);
  transition: all .2s ease-in-out; }

.counter-panel-animation {
  -webkit-transform: scale(0.7);
          transform: scale(0.7); }

.scaled {
  -webkit-transform: scale(0.6);
          transform: scale(0.6); }

.item {
  /*   background-color:red; */
  font-size: 120px; }

.number {
  /*   background-color: green; */
  font-size: 240px;
  text-align: right;
  padding-right: 25px; }

.item-wrapper {
  height: 100%; }

.item:nth-child(1) {
  /*     background-color: coral; */
  line-height: 0.95; }

.item:nth-child(2) {
  /*     background-color: lightblue; */
  line-height: 0.9; }

.boxed {
  border: 5px solid;
  border-color: #fff;
  box-shadow: -10px 0 40px #4CCEB1, 10px 0 40px #E0F0CF;
  border-radius: 30px; }

.info-text {
  position: absolute;
  color: #4CCEB1;
  letter-spacing: 1px;
  margin-top: 10px;
  right: 50px; }

.smile_transform {
  transition: all 1s ease; }

.visible_smile {
  position: absolute;
  -webkit-transform: scale(7);
          transform: scale(7);
  -webkit-animation: turner 0.8s ease;
          animation: turner 0.8s ease; }

@-webkit-keyframes turner {
  from {
    -webkit-transform: scale(0);
            transform: scale(0); }
  to {
    -webkit-transform: scale(7);
            transform: scale(7); } }

@keyframes turner {
  from {
    -webkit-transform: scale(0);
            transform: scale(0); }
  to {
    -webkit-transform: scale(7);
            transform: scale(7); } }

.hidden_smile {
  position: absolute;
  visibility: hidden; }

.Camera {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 100px; }

.face-marker {
  /*Horizontal*/
  background-image: linear-gradient(to right, black 33%, rgba(255, 255, 255, 0) 0%);
  background-position: bottom;
  background-size: 3px 1px;
  background-repeat: repeat-x;
  /*Vertical*/
  background-image: linear-gradient(black 33%, rgba(255, 255, 255, 0) 0%);
  background-position: right;
  background-size: 1px 3px;
  background-repeat: repeat-y; }

