@import url(https://fonts.googleapis.com/css?family=Dosis:800);


body{
    font-family: 'Dosis', sans-serif;

    margin:0;
  }

  .canvas{
      // background-color:red;
    width:100%;
    height:100vh;
    display:flex;
    flex-direction:column;
    align-items:center;
  }

  .counter-panel{
    color: white;
    display:flex;
    justify-content:flex-end;
    align-items:center;

    padding:15px;
    padding-left:50px;
    padding-right:50px;

    height:250px;
    width:auto;

    position:absolute;
    bottom:0px;

    /*    background:linear-gradient(rgba(156,39,176,.2),#fff); */
    background:linear-gradient(45deg, #4CCEB1, #E0F0CF);


    transition: all .2s ease-in-out;
  }

  .counter-panel-animation {
    transform: scale(0.7);
  }

  .scaled {
    transform: scale(0.6);
  }

  .item{
    /*   background-color:red; */
    font-size: 120px;
  }

  .number{
    /*   background-color: green; */
    font-size: 240px;
    text-align:right;
    padding-right:25px;
  }

  .item-wrapper{
    height:100%;

  }

  .item:nth-child(1){
    /*     background-color: coral; */
    line-height:0.95;
  }

  .item:nth-child(2){
    /*     background-color: lightblue; */
    line-height:0.9;
  }

  //.grow { transition: all .2s ease-in-out; }
  //.grow:hover { transform: scale(1.1); }

  .boxed {
    border: 5px solid;
    border-color: #fff;
    box-shadow: -10px 0 40px #4CCEB1,
      10px 0 40px #E0F0CF;
    border-radius: 30px;
  }

  .info-text{
    position:absolute;
    // background-color:red;
    color: #4CCEB1;
    letter-spacing: 1px;
    margin-top: 10px;
    right: 50px;
  }

  .smile_transform {
    -webkit-transition: all 1s ease;
    -moz-transition: all 1s ease;
    -o-transition: all 1s ease;
    -ms-transition: all 1s ease;
    transition: all 1s ease;
  }

  .visible_smile {
    position:absolute;
    transform: scale(7);
    animation: turner 0.8s ease;

  }

  @keyframes turner{
    from{  transform: scale(0) }
    to  {  transform: scale(7) }
  }

  .hidden_smile{
    position:absolute;
    visibility:hidden;
  }

  .Camera {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
  }

  .face-marker{
    /*Horizontal*/
    background-image: linear-gradient(to right, black 33%, rgba(255,255,255,0) 0%);
    background-position: bottom;
    background-size: 3px 1px;
    background-repeat: repeat-x;

    /*Vertical*/
    background-image: linear-gradient(black 33%, rgba(255,255,255,0) 0%);
    background-position: right;
    background-size: 1px 3px;
    background-repeat: repeat-y;
  }
  
  
  